<template>
    <li
        :key="node.id"
        :class="[node.done? 'done': '']"
    >

        <input
            type="checkbox"
            :id="'todo-'+ node.id"
            v-model="node.done"
        />

        <label v-if="!node.edit" :for="'todo-' + node.id">{{ node.text }}</label>

        <input v-else
            v-model="node.text"
            type="text"
            @keyup.enter="save_task(node)"
            class="edit"
        />

        <a
            @click="node.edit ? save_task(node) : edit_task(node)"
            class='edit'
            v-html="node.edit ? '&#128190;' : '&#9998;'"
        ></a>

        <a
            @click="remove_task(node)"
            class="remove"
        >&#215;</a>

    </li>
</template>

<script>
    export default {
        props: {
            node: {
                type: Object,
                required: true
            }
        },
        methods: {
            remove_task: function() {
                this.$emit('remove_task', this.node);
            },
            edit_task: function() {
                this.$emit('edit_task', this.node);
            },
            save_task: function() {
                this.$emit('save_task', this.node);
            }
        }
    }
</script>


<style scoped lang="scss">
    li {
    $padding: 10px;
    $size: 20px;
    $right: 10px;

    margin: 0;
    background-color: #ffff;
    border-radius: 4px;
    
    padding: 10px;
    padding-right: $padding + $size + $right;
    position: relative;
    display: flex;
    flex-grow: 1;
    

    + li {
        margin-top: 6px;
    }

    input {
      display: flex;
    }

    &:hover {
        transition: 300ms;
        transform: scale(1.02);
    }

    &.done {
        opacity: 0.5;
    }
    
    label {
        margin-left: 10px;
        cursor: pointer;
        overflow: hidden;
        flex-wrap: wrap;
        word-break: break-word;
        white-space: pre-wrap;
    }

    a.remove {
        position: absolute;
        width: $size;
        height: $size;
        line-height: $size;
        text-align: center;
        color: #555;
        cursor: pointer;
        right: $right;
        top: 50%;
        margin-top: -($size / 2);
    }

    a.edit {
        position: absolute;
        right: 40px;
        width: $size;
        height: $size;
        line-height: $size;
        text-align: center;
        color: #555;
        cursor: pointer;
        top: 50%;
        margin-top: -10px;
    }
  }
</style>
