<template>
  <div id="app">
    <h1>üìù Todo List</h1>

    <div class="input_area">
      <input 
        v-model="new_task" 
        type="text"
        placeholder="add a new task..." 
        @keyup.enter="add_task"/>
        

      <button @click="add_task">Add</button>
    </div>

    <ul v-if="todos.length">
      <li
          is="todo_item"
          v-for="todo in todos"
          :key="todo.id"
          :node="todo"
          @remove_task="remove_task"
          @edit_task="edit_task"
          @save_task="save_task"
      >
      </li>
    </ul>

    <p v-else>Todo list is empty.</p>

  </div>
</template>

<script>
import todo_item from './components/todo_item.vue'

export default {
  name: 'App',

  components: {
    todo_item
  },

  data: function() {
    return {
      new_task: '',
      todos: [],
    };
  },

  methods: {
    get_task_id: function(){
      return Math.floor(Math.random() * 10000);
    },

    add_task: function() {
      if (this.new_task.trim() !== '') {
          this.todos.push({
              id: this.get_task_id(),
              text: this.new_task.trim(),
              done: false,
              edit: false
          });

          this.new_task = '';
        }
      },

    remove_task:function(todo) {
      let _index = this.todos.indexOf(todo);

            if( _index > -1 ) {
                this.todos.splice(_index, 1);
            }
    },

    edit_task:function(todo) {
      todo.edit = true;
    },

    save_task:function(todo) {
      todo.edit = false;
    }
  }
}
</script>

